"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { useAuth } from "@/lib/context/AuthContext";
import { signInSchema, SignInFormData } from "@/lib/validations/auth";
import Navbar from "@/app/components/Navbar";

export default function LoginPage() {
  const router = useRouter();
  const { signIn } = useAuth();
  const [error, setError] = useState<string>("");
  const [loading, setLoading] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<SignInFormData>({
    resolver: zodResolver(signInSchema),
  });

  const onSubmit = async (data: SignInFormData) => {
    try {
      setError("");
      setLoading(true);
      await signIn(data.email, data.password);
      router.push("/dashboard");
    } catch (err: any) {
      console.error(err);
      if (err.code === "auth/invalid-credential" || err.code === "auth/user-not-found" || err.code === "auth/wrong-password") {
        setError("E-posta veya şifre hatalı. Lütfen kontrol edip tekrar deneyin.");
      } else if (err.code === "auth/too-many-requests") {
        setError("Çok fazla başarısız deneme. Lütfen daha sonra tekrar deneyin.");
      } else if (err.code === "auth/user-disabled") {
        setError("Bu hesap askıya alınmış. Destek ile iletişime geçin.");
      } else if (err.code === "auth/invalid-email") {
        setError("Geçersiz e-posta adresi.");
      } else {
        setError("Giriş yaparken bir hata oluştu. Lütfen tekrar deneyin.");
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <Navbar />
      <div className="flex min-h-screen items-center justify-center bg-[#fbfbfd] px-6 pt-[48px]">
        <div className="w-full max-w-[400px]">
          <div className="animate-fade-in-up mb-10 text-center">
            <div className="mx-auto mb-6 flex h-16 w-16 items-center justify-center rounded-2xl bg-gradient-to-br from-[#0071e3] to-[#00c7be] shadow-lg shadow-[#0071e3]/20">
              <svg className="h-8 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={1.5}>
                <path strokeLinecap="round" strokeLinejoin="round" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
              </svg>
            </div>
            <h1 className="text-[32px] font-semibold leading-[1.08] tracking-[-0.02em] text-[#1d1d1f] sm:text-[40px]">
              Giriş Yap
            </h1>
            <p className="mt-3 text-[17px] leading-[1.47] text-[#6e6e73]">
              Hesabınıza erişin
            </p>
          </div>

          <div className="animate-fade-in-up animate-delay-100">
            <div className="rounded-3xl bg-white p-8 shadow-lg shadow-black/[0.03] ring-1 ring-black/[0.04]">
              {error && (
                <div className="mb-6 flex items-start gap-3 rounded-2xl bg-[#ff3b30]/8 p-4">
                  <svg className="mt-0.5 h-5 w-5 flex-shrink-0 text-[#ff3b30]" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
                    <path strokeLinecap="round" strokeLinejoin="round" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                  </svg>
                  <p className="text-[14px] leading-[1.43] text-[#ff3b30]">{error}</p>
                </div>
              )}

              <form onSubmit={handleSubmit(onSubmit)} className="space-y-5">
                <div>
                  <label htmlFor="email" className="mb-2 block text-[14px] font-medium text-[#1d1d1f]">
                    E-posta Adresi
                  </label>
                  <input
                    {...register("email")}
                    type="email"
                    id="email"
                    autoComplete="email"
                    className="h-[52px] w-full rounded-xl border border-[#d2d2d7] bg-white px-4 text-[17px] text-[#1d1d1f] outline-none transition-all duration-200 placeholder:text-[#86868b] hover:border-[#aeaeb2] focus:border-[#0071e3] focus:ring-4 focus:ring-[#0071e3]/12"
                    placeholder="ornek@email.com"
                  />
                  {errors.email && (
                    <p className="mt-2 text-[13px] text-[#ff3b30]">{errors.email.message}</p>
                  )}
                </div>

                <div>
                  <label htmlFor="password" className="mb-2 block text-[14px] font-medium text-[#1d1d1f]">
                    Şifre
                  </label>
                  <input
                    {...register("password")}
                    type="password"
                    id="password"
                    autoComplete="current-password"
                    className="h-[52px] w-full rounded-xl border border-[#d2d2d7] bg-white px-4 text-[17px] text-[#1d1d1f] outline-none transition-all duration-200 placeholder:text-[#86868b] hover:border-[#aeaeb2] focus:border-[#0071e3] focus:ring-4 focus:ring-[#0071e3]/12"
                    placeholder="••••••••"
                  />
                  {errors.password && (
                    <p className="mt-2 text-[13px] text-[#ff3b30]">{errors.password.message}</p>
                  )}
                </div>

                <button
                  type="submit"
                  disabled={loading}
                  className="relative mt-2 flex h-[52px] w-full items-center justify-center rounded-xl bg-[#0071e3] text-[17px] font-medium text-white transition-all duration-200 hover:bg-[#0077ed] active:scale-[0.98] disabled:cursor-not-allowed disabled:opacity-50"
                >
                  {loading ? (
                    <div className="h-5 w-5 animate-spin rounded-full border-2 border-white/30 border-t-white" />
                  ) : (
                    "Giriş Yap"
                  )}
                </button>
              </form>
            </div>

            <div className="mt-8 text-center">
              <p className="text-[15px] text-[#6e6e73]">
                Hesabınız yok mu?{" "}
                <Link href="/auth/signup" className="font-medium text-[#0071e3] transition-colors hover:text-[#0077ed]">
                  Kayıt Ol
                </Link>
              </p>
            </div>

            <div className="mt-6 rounded-2xl bg-[#f5f5f7] p-5">
              <div className="flex items-start gap-3">
                <div className="flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-full bg-[#0071e3]/10">
                  <svg className="h-4 w-4 text-[#0071e3]" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth={2}>
                    <path strokeLinecap="round" strokeLinejoin="round" d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                </div>
                <p className="text-[14px] leading-[1.5] text-[#6e6e73]">
                  Henüz hesabınız yoksa yukarıdaki "Kayıt Ol" linkine tıklayarak hızlıca hesap oluşturabilirsiniz.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
